import React from 'react'
import './Author.css'
import './Responsive.css';
import image01 from './imgs/girl-1848954.jpg'


export const Author = () => {
    return (
        <>
            <div id="Author-container">
                 <div id="Author-content">
                     <h1 id="Author-content-headline">Author Name</h1>
                     <p id="Author-content-description">Author Description Lorem ipsum dolor sit amet consectetur adipisicing elit. </p>
                     </div>
                 <div id="Author-Image">
                     <img src={image01} alt="img" />
                 </div>

            </div>
            
        </>
    )
}
