import './App.css';
import { Header } from "./Components/Header";
import { Section } from "./Components/Section";
import { Cards } from "./Components/Cards";
import { Author } from "./Components/Author";
import { Footer } from "./Components/Footer";
import { Gallery } from "./PagesComponents/Gallery";
import { Services } from "./PagesComponents/Services";
import { About } from "./PagesComponents/About";
import { useState } from 'react';
import { Route } from 'react-router-dom'

function App() {

  const [theam, setTheam] = useState(true);


  function one() {

    theam ? document.body.className = "dark" : document.body.className = "light";

    setTheam(!theam)
  }

  return (
    <>

      <Header theam={one} theamcol={theam} />
      
      <Route path="/services" > <Services /> </Route>
      <Route path="/gallery" > <Gallery/></Route>
      <Route path="/about" > <About /></Route>

      <Route path="/" exact={true} >
        <Section />
        <Cards />
        <Author />
      </Route>
      
      <Footer />  
    </>
  );
}

export default App;
