import React from 'react'
import './header.css'
import './Responsive.css';

import image01 from './icons/angular.svg'
import image02 from './icons/moon-regular.svg'
import image03 from './icons/sun-solid.svg'
import {Link} from 'react-router-dom'

export const Header = (props)=>{

    
    return (
        <>
        <div className="container">
            <div id="company-Logo">
                <img src={image01} alt="company_Logo" />
            </div>
            <ul id="navbar">
                <li className="navItems"><Link to="/">Home</Link></li>
                <li className="navItems"><Link to="/services">Services</Link></li>
                <li className="navItems"><Link to="/gallery">Gallery</Link></li>
                <li className="navItems"><Link to="/About">About</Link></li>
            </ul>
            

          <div id="theam">
           <img className="filter" onClick={()=>props.theam()} src={props.theamcol?image02:image03} alt="company_Logo" />
          </div>
     
        </div>
        </>
    )
}
